import { Editor } from '@tiptap/core'
import StarterKit from '@tiptap/starter-kit'
import Underline from '@tiptap/extension-underline'
import Link from '@tiptap/extension-link'
import Placeholder from '@tiptap/extension-placeholder'

/**
 * Strata Editor factory function for Alpine.js integration
 * Creates a rich text editor component with Tiptap
 */
export function createStrataEditor(config = {}) {
  let editor; // Store editor outside Alpine's reactive system
  
  return {
    htmlContent: typeof config.value === 'string' ? config.value : '',
    updatedAt: Date.now(), // Force Alpine to rerender on editor changes
    disabled: config.disabled || false,
    
    async init() {
      try {
        const _this = this;
        
        // Create the editor instance
        editor = new Editor({
          element: this.$refs.editorElement,
          extensions: [
            StarterKit.configure({
              heading: {
                levels: [1, 2, 3],
              },
            }),
            Underline,
            Link.configure({
              openOnClick: false,
              HTMLAttributes: {
                class: 'text-primary hover:text-primary-700 underline cursor-pointer',
              },
            }),
            Placeholder.configure({
              placeholder: config.placeholder || 'Write something...',
            }),
          ],
          content: _this.htmlContent || '',
          editable: !config.disabled,
          editorProps: {
            attributes: {
              class: 'prose prose-sm dark:prose-invert max-w-none focus:outline-hidden p-3',
              style: config.minHeight ? `min-height: ${config.minHeight - 24}px;` : '',
            },
          },
          onCreate({ editor }) {
            _this.updatedAt = Date.now();
          },
          onUpdate({ editor }) {
            _this.htmlContent = editor.getHTML();
            _this.updatedAt = Date.now();
          },
          onSelectionUpdate({ editor }) {
            _this.updatedAt = Date.now();
          },
        });
      } catch (error) {
        console.error('Failed to initialize Strata Editor:', error);
      }
    },
    
    isLoaded() {
      return editor;
    },
    
    isActive(type, opts = {}) {
      return editor ? editor.isActive(type, opts) : false;
    },

    // Toolbar methods
    toggleBold() {
      if (editor) editor.chain().focus().toggleBold().run();
    },

    toggleItalic() {
      if (editor) editor.chain().focus().toggleItalic().run();
    },

    toggleHeading(level) {
      if (editor) editor.chain().focus().toggleHeading({ level }).run();
    },

    toggleBulletList() {
      if (editor) editor.chain().focus().toggleBulletList().run();
    },

    toggleOrderedList() {
      if (editor) editor.chain().focus().toggleOrderedList().run();
    },

    undo() {
      if (editor) editor.chain().focus().undo().run();
    },

    redo() {
      if (editor) editor.chain().focus().redo().run();
    },

    canUndo() {
      return editor ? editor.can().undo() : false;
    },

    canRedo() {
      return editor ? editor.can().redo() : false;
    },
    
    toggleLink() {
      if (!editor) return;
      
      if (editor.isActive('link')) {
        editor.chain().focus().unsetLink().run();
      } else {
        const url = prompt('Enter URL:');
        if (url) {
          editor.chain().focus().setLink({ href: url }).run();
        }
      }
    },
    
    destroy() {
      if (editor) {
        editor.destroy();
        editor = null;
      }
    }
  };
}

// Auto-register with Alpine.js if it's available
if (typeof window !== 'undefined') {
  // Wait for Alpine to be available
  document.addEventListener('alpine:init', () => {
    if (window.Alpine) {
      window.Alpine.data('strataEditor', createStrataEditor);
    }
  });
  
  // Also make it available globally for manual registration
  window.createStrataEditor = createStrataEditor;
}